.Break {
    grid-column: 1 / 2;
    grid-row: 2 / 3;
    -webkit-box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.75);
    -moz-box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.75);
    box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.75);
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 10px;
}

.control {
    display: flex;
    justify-content: space-evenly;
    width: 100%;
}

.btn {
    padding: 0;
    font-size: 25px;
    color: #283739;
    cursor: pointer;
    background: none;
    border: none;
}

.btn:hover {
    color: #2c5d63;
}